body {
  background: #fdfdfd;
}

img {
  vertical-align: middle;
  border-style: none;
  width: 100%;
}

.mb-4 {
  margin-bottom: 1.5rem;
}

.form-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 460px;
}

.steps {
  display: flex;
  justify-content: space-between;
}

.form:first-child {
  margin-bottom: 24px;
}

.step {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 0;
  align-items: center;
}

.step:not(:first-child)::before {
  height: .2em;
  width: 100%;
  bottom: 0;
  left: -50%;
  top: 1rem;
  content: " ";
  display: block;
  position: absolute;
  background: #b5b5b5;
}

.step__marker {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  border-radius: 50%;
  font-weight: 700;
  justify-content: center;
  background: #b5b5b5;
  color: #fff;
  border: 4px solid #fff;
  z-index: 1;
}

.is-completed:not(:first-child)::before {
  background: #f26120;
}

.is-completed .step__marker {
  background: #f26120;
  border: 4px solid #f26120;
}

.is-active:not(:first-child)::before {
  background: #f26120;
}

.is-active .step__marker {
  background: #19ddfe;
  border: 4px solid #f26120;
}

.step__title {
  font-weight: 700;
}

.invalid-feedback {
  display: block;
}


input.invalid,
select.invalid {
  border-color: #dc3545;
}

.custom-file-label.invalid {
  border-color: #dc3545;
}

